<script src="../../../config/index.js"></script>
<template>
  <div class="clearfix">
    <div class="operation">
      <div class="clearfix ">
        <el-form label-width="80px">
          <!--<el-col :span="4">-->
            <!--<el-form-item ><el-button icon="el-icon-search" type="primary" @click="searchList">查询</el-button></el-form-item>-->
          <!--</el-col>-->
          <el-col :span="5">
            <el-form-item label="审核结果">
              <el-select
                v-model="tempData.status"
                @change="selectStatus"
                placeholder="请选择审核结果查询">
                <el-option label="全部" value="">全部</el-option>
                <el-option label="审核中" value="0"></el-option>
                <el-option label="审核通过" value="1"></el-option>
                <el-option label="审核失败" value="2"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="开户人">
              <el-input v-model="tempData.realname">
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4" style="padding-left:40px">
            <el-button type="primary" @click="search" icon="el-icon-search">查询</el-button>
          </el-col>
        </el-form>
      </div>
    </div>
    <div class="table">
      <el-table
        border
        :data="dataList"
        style="width: 100%;margin-top:20px;"
      >
        <el-table-column
          fixed
          label="序号"
          width="80"
          align="center"
          type="index" >
        </el-table-column>
        <el-table-column
          prop="bankName"
          label="银行名称"
          width="200"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="cardNumber"
          label="银行卡号"
          width="250"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="categorys"
          label="账户类别"
          width="200"
          align="center" >
          <template slot-scope="scope">
            <span v-if="scope.row.categorys == 1">配送员</span>
            <span v-else-if="scope.row.categorys == 2">商户店铺</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="realname"
          label="开户人"
          width="200"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="status"
          label="审核状态"
          width="120"
          align="center" >
          <template slot-scope="scope">
            <span v-if="scope.row.status == 0">审核中</span>
            <span v-else-if="scope.row.status == 1">审核通过</span>
            <span v-else-if="scope.row.status == 2">审核失败</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="创建时间"
          width="220"
          align="center" >
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button type="primary" @click="editor(scope.row.id)">审核</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <pagination
      :current-page="page.currentPage"
      :page-size="page.pageSize"
      :total="page.pageNum"
      @sizeChange="sizeChange"
      @currentChange="currentChange"
    >
    </pagination>
    <el-dialog title="审核" :visible.sync="editorVisable" width="20%" center="true">
      <el-form >
        <el-form-item label="审核">
          <el-select v-model="editorItem.status" placeholder="请选择审核">
            <el-option label="默认" value=""></el-option>
            <el-option label="审核通过" value="1"></el-option>
            <el-option label="审核失败" value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editorVisable = false">取 消</el-button>
        <el-button type="primary" @click="updateExamine">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import { bankcardInfo, bankcardInfoUpdate } from "@/api/index.js";
  import pagination from "@/components/pagination";
  export default {
    components: {
      pagination
    },
    data() {
      return {
        dataList: [],
        //搜索时的临时数据
        tempData: {
          realname: '',
          status: ''
        },
        //搜索时的数据
        serData: {},
        //分页信息
        page: {
          pageNum: 10,
          currentPage: 1,
          pageSize: 5
        },
        editorVisable: false,
        editorItem: {
          status: ''
        },
      };
    },
    methods: {
      //加载数据
      initData(page, size) {
        bankcardInfo({
          page: page || this.page.currentPage,
          size: size || this.page.pageSize,
          realname: this.tempData.realname,
          status: this.tempData.status
        }).then(res => {
          this.dataList = res.rows;
          this.page.pageNum = res.count;
        });
      },
      editor(id){
        console.log('---->', id);
        this.editorVisable=true;
        this.editorItem['id'] = id;
        // this.editorItem=obj;
      },
      updateExamine() {
        bankcardInfoUpdate(this.editorItem).then( res => {
          this.editorVisable=false;
          this.initData(1);
        })
      },
      //页数改变时的回调
      currentChange(val) {
        this.page.currentPage = val;
        this.initData();
      },
      //每页总数改变时的回调
      sizeChange(val) {
        this.page.pageSize = val;
        this.initData(1);
      },
      selectStatus() {
        this.initData(1);
      },
      //条件筛选店铺
      search() {
        this.initData(1);
      }
    },
    beforeMount() {
      this.initData();
    }
  };
</script>

<style>
  .addBatchBtn a {
    color: #ffffff;
  }
  .content {
    /*min-width: 1200px;*/
    background: rgb(255, 255, 255);
    padding: 26px 30px;
    position: relative;
    /*min-height: 800px;*/
  }
  h4 {
    text-align: left;
    color: #333333;
    font-size: 18px;
    font-family: PingFang-SC-Bold;
    padding-bottom: 25px;
    border-bottom: 1px solid #e1e1e1;
  }
</style>



